import { Component, OnInit } from '@angular/core';
import { LoginService } from './../../services/login.service'
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;
  forgotForm: FormGroup;
  verifytForm: FormGroup;
  isSubmit: boolean = false;
  isForgotSubmit: boolean = false;
  isError: boolean = false;
  incorectPassword: boolean = false;
  checked: boolean = false
  isForgot: boolean = false

  constructor(private router: Router, private loginService: LoginService) {
    this.loginForm = new FormGroup({
      username: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [Validators.required]),
      appType: new FormControl(4, [Validators.required]),
      agreement: new FormControl(),
      keepLogin: new FormControl(false),
    });

    this.forgotForm = new FormGroup({
      username: new FormControl('', [Validators.required, Validators.email]),
      question: new FormControl(''),
    })

    this.verifytForm = new FormGroup({
      answer: new FormControl('', [Validators.required]),
    })
  }

  ngOnInit(): void {}

  onSubmit() {
    if (!this.loginForm.value.agreement) return
    if (this.loginForm.valid) {
      this.isSubmit = true
      this.loginService.login(this.loginForm.value).subscribe((res) => {
        this.isSubmit = false
        if (res.loginResult.result === 'Success') {
          console.log(res);
          this.loginService.setData("allowedRole", res.loginResult.allowedRole)
          this.loginService.setData("customerID", res.loginResult.customerID)
          this.loginService.setData("encryptedTicket", res.loginResult.encryptedTicket)
          this.loginService.setData("accessToken", res.loginResult.accessToken)
          this.loginService.setData("refreshToken", res.loginResult.refreshToken)
          this.loginService.setData("hmpServicePath", res.loginResult.hmpServicePath)
          this.loginService.setData("userID", res.loginResult.userID)
          this.registerClient()
          this.router.navigateByUrl('/pages')
        }
        if (res.loginResult.result === 'Error') {
          this.isError = true
          setTimeout(() => {
            this.isError = false
          }, 2000);
        }
        if (res.loginResult.result === 'Failed') {
          this.incorectPassword = true
          this.loginForm.controls['password'].setErrors({ invalid: true });
        }
      }, (err) => {
        console.log(err)
      })
    }
  }

  onForgotSubmit(){
    if (this.forgotForm.valid) {
      let obj = {
        userName: this.forgotForm.value.username,
        secretAnswer: ''
      }
      this.loginService.getSecretQuestion(obj).subscribe((res) => {
        console.log(res);
        
        this.isForgotSubmit = true
        this.forgotForm.patchValue({
          question: res.baseResult
        })
      }, (err) => {
        console.log(err)
      })
    }
  }

  onVerifySubmit(){
    if (this.verifytForm.valid) {
      let obj = {
        userName: this.forgotForm.value.username,
        secretAnswer: this.verifytForm.value.answer
      }
      this.loginService.verifySecretAnswer(obj).subscribe((res) => {
        console.log(res);
      }, (err) => {
        console.log(err)
      })
    }
  }


  registerClient(){
    let obj = {
      customerID: this.loginService.getData("customerID"),
      userID: this.loginService.getData("userID"),
      userType: 'Manager',
      encryptedTicket: this.loginService.getData("encryptedTicket"),
    }

    console.log(obj, "registerClient object");
  
    this.loginService.registerClient(obj).subscribe((res) => {
      console.log(res, "registerClient response");
      if (res.baseResult.result === 'Success') { 
        console.log(res, "registerClient");
        this.loginService.setData("user", JSON.stringify(res.baseResult.user))
        this.loginService.setData("setting", JSON.stringify(res.baseResult.setting))
        if (res.baseResult.setting.supervisorAccess) this.registerSupervisor()
      } else {
        console.log(res.baseResult.result); // error message will be on screen
      }
    }, (err) => {
      console.log(err)
    })
  }

  registerSupervisor(){
    let obj = {
      customerID: this.loginService.getData("customerID"),
      userID: this.loginService.getData("userID"),
    }
    this.loginService.registerSupervisor(obj).subscribe((res) => {
      console.log(res);
     }, (err) => {
      console.log(err)
    })
  }

  checkstate(event: any) {
    if (!event.checked) {
      this.loginForm.controls["agreement"]?.setValidators(Validators.required)
    } else {
      this.loginForm.controls["agreement"]?.clearValidators();
    }
    this.loginForm.controls["agreement"]?.updateValueAndValidity();
  }

  forgotPassword(){
    this.isForgot = true
  }
}
